            pic: 'icon-inputs',
            content: '输入框组件',
            target: '_blank'
          },
          {
            link: '../demo/cell.html',
            pic: 'icon-cell',
            content: 'cell组件',
            target: '_blank'
          },
          {
            link: '../demo/loading.html',
            pic: 'icon-loading',
            content: 'loading组件',
            target: '_blank'
          },
          {
            link: '../demo/popups.html',
            pic: 'icon-popups',
            content: '弹窗组件',
            target: '_blank'
          },
          {
            link: '../demo/tabbar.html',
            pic: 'icon-tabbar',
            content: 'tabbar组件',
            target: '_blank'
          },
          {
            link: '../demo/toast.html',
            pic: 'icon-toast',
            content: 'toast组件',
            target: '_blank'
          },
          {
            link: '../demo/buttons.html',
            pic: 'icon-buttons',
            content: 'button组件',
            target: '_blank'
          },
          {
            link: '../demo/page.html',
