            content: 'page分页组件',
            target: '_blank'
          },
          {
            link: '../demo/switchs.html',
            pic: 'icon-switchs',
            content: 'switch开关',
            target: '_blank'
          },
          {
            link: '../demo/folder.html',
            pic: 'icon-folder',
            content: 'folder折叠面板',
            target: '_blank'
          },
          {
            link: '../demo/progressBar.html',
            pic: 'icon-progress',
            content: 'progressBar进度条组件',
            target: '_blank'
          },
          {
            link: '../demo/inputNum.html',
            pic: 'icon-inputNum',
            content: '加减计数器',
            target: '_blank'
          },
          {
            link: '../demo/stepbar.html',
            pic: 'icon-step',
            content: '步骤条',
            target: '_blank'
          }
        ]
      }
    },
    methods: {
      transPage: function (link) {
        console.log(link)
        window.location.href = link
      }
    },
    events: {
      'transPage': function (link) {
        console.log(link)
        window.location.href = 'link'
      }
    }
  }
</script>

<style scoped>
  @import '../static/common.scss';
</style>



